import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { environment } from 'src/environments/environment';


export interface IJobDuty  {
  id: string;
  name: string;
  // description?: string;
}


@Component({
  selector: 'app-job-duty',
  templateUrl: './job-duty.component.html',
  styleUrls: ['./job-duty.component.scss']
})
export class JobDutyComponent implements OnInit {

  public entity = 'job-duty';
  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  public jobDuties: IJobDuty[] = [];
  public staticJobDuties: IJobDuty[] = [
    {
      id: '1',
      name: 'Job A'
    },
    {
      id: '2',
      name: 'Job B'
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private router: Router,
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.buildForm();

    // TEMP
    if (environment.staticData) {
      this.jobDuties = this.staticJobDuties;
      return;
    }

    this.apiService.list(this.entity, {limit: 100, offset: 0}).subscribe( x => {
      this.jobDuties = x;
    });
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]]
    });
  }

  showAppForm(type: string, br?: IJobDuty): void {
    if (type === 'add') {
      this.modalTitle = 'New Business Job Duty';
    } else {
      this.modalTitle = `Edit ${br.name}`;
      this.form.setValue(br);
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name }: IJobDuty = this.form.value;

    if (id) {
      this.update({id, name });
    } else {
      this.create({ name });
    }

    this.form.reset();
  }

  deleteApp(app: IJobDuty): void {
    const sure = confirm('Are you sure?');
    if (sure) {

      // TEMP
      if (environment.staticData) {
        this.jobDuties = this.jobDuties.filter( o => o.id !== app.id);
        return;
      }

      this.apiService.delete(this.entity, app.id).subscribe( x => {
        this.jobDuties = this.jobDuties.filter( o => o.id !== app.id);
      });
    }
  }


  private update({id, name}): void{

    // TEMP
    if (environment.staticData) {
      this.jobDuties.map( x => {
        if (id === x.id) {
          x.name = name;
        }
      });
      return;
    }

    this.apiService.update(this.entity, id, {
      name
    }).subscribe ( x => {
      this.jobDuties.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
    });
  }

  private create({ name }): void{
    // TEMP
    if (environment.staticData) {
      this.jobDuties.push({
        id: new Date().getTime().toString(),
        name
      });
      return;
    }

    this.apiService.create(this.entity, {
      name
    }).subscribe ( x => {
        this.jobDuties.push({
          id: x.id,
          name
        });
    });
  }

  selectApp(app: IJobDuty): void {
    // this.localDataService.save('application', app);
    // this.router.navigate(['/businessroles/', app.id]);
  }
}
